import React, { useState, useRef, useEffect, useCallback } from "react";
import { useParams, useNavigate } from "react-router-dom";
import Webcam from "react-webcam";
import html2canvas from "html2canvas";
import { initializeApp } from "firebase/app";
import { getStorage, ref, uploadBytes } from "firebase/storage";
import { getFirestore, collection, addDoc } from "firebase/firestore";
import "./PictureSize2.css";

const base64ToBlob = (base64) => {
    const byteString = atob(base64.split(",")[1]);
    const mimeString = base64.split(",")[0].split(":")[1].split(";")[0];
    const ab = new ArrayBuffer(byteString.length);
    const ia = new Uint8Array(ab);
    for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: mimeString });
};

const PictureSize2 = () => {
const webcamRef = useRef(null);
const resultZoneRef = useRef(null);
const navigate = useNavigate();
const [showModal, setShowModal] = useState(true);
const [countdown, setCountdown] = useState(10);
const [photoCount, setPhotoCount] = useState(0);
const [capturedImages, setCapturedImages] = useState([]);
const [capturedImageUrls, setCapturedImageUrls] = useState([]);
const [imagesLoaded, setImagesLoaded] = useState(false);
const [finalImageBlob, setFinalImageBlob] = useState(null); //   
const { frame } = useParams();

const frameImages = {
    designer: "/images/designer2.png",
    developer: "/images/developer2.png",
    summer1: "/images/summer1-2.png",
    summer2: "/images/summer2-2.png",
    summer3: "/images/summer3-2.png",
    teacher1: "/images/white2.png",
    teacher2: "/images/ham2.png",
    teacher3: "/images/lee2.png",
};

const poseImages = {
    teacher1: ["/images/white2-1.png", "/images/white2-2.png", "/images/white2-3.png", "/images/white2-4.png"],
    teacher2: ["/images/ham2-1.png", "/images/ham2-2.png", "/images/ham2-3.png", "/images/ham2-4.png"],
    teacher3: ["/images/lee2-1.png", "/images/lee2-2.png", "/images/lee2-3.png", "/images/lee2-4.png"],
};

// capturedImages Blob -> Blob URL   
useEffect(() => {
    const urls = capturedImages.map((blob) => URL.createObjectURL(blob));
    setCapturedImageUrls(urls);

    return () => {
      urls.forEach((url) => URL.revokeObjectURL(url));
    };
  }, [capturedImages]);

  const takePhoto = useCallback(() => {
    if (photoCount < 4) {
      capturePhoto();
      setCountdown(10);
      setPhotoCount((prevCount) => prevCount + 1);
    }
  }, [photoCount]);

  useEffect(() => {
    if (showModal) {
      const modalTimer = setTimeout(() => setShowModal(false), 3000);
      return () => clearTimeout(modalTimer);
    }

    if (countdown > 0 && photoCount < 4) {
      const timer = setTimeout(() => setCountdown((prev) => prev - 1), 1000);
      return () => clearTimeout(timer);
    } else if (countdown === 0 && photoCount < 4) {
      takePhoto();
    }
  }, [countdown, photoCount, showModal, takePhoto]);

  const capturePhoto = () => {
    if (webcamRef.current) {
      const imageSrc = webcamRef.current.getScreenshot();
      if (imageSrc) {
        const imageBlob = base64ToBlob(imageSrc);
        setCapturedImages((prevImages) => [...prevImages, imageBlob]);
      }
    }
  };

  //     
  const waitForImagesToLoad = (element) => {
    const images = element.querySelectorAll("img");
    const promises = Array.from(images).map((img) => {
      if (img.complete && img.naturalHeight !== 0) {
        return Promise.resolve();
      }
      return new Promise((resolve) => {
        const timeout = setTimeout(() => {
          console.warn('Image load timeout:', img.src);
          resolve();
        }, 5000); // 5 
        
        img.onload = () => {
          clearTimeout(timeout);
          resolve();
        };
        img.onerror = () => {
          clearTimeout(timeout);
          console.error('Image load error:', img.src);
          resolve();
        };
      });
    });
    return Promise.all(promises);
  };

  //      
  const captureResultZoneAsBlob = useCallback(async () => {
    if (!resultZoneRef.current || capturedImageUrls.length !== 4) {
      console.error('Result zone ref is null or images not ready');
      return null;
    }

    try {
      console.log('Starting manual canvas creation...');
      
      //    (CSS  )
      const canvasWidth = 590;
      const canvasHeight = 879;
      
      //  
      const scale = 2;
      const canvas = document.createElement('canvas');
      canvas.width = canvasWidth * scale;
      canvas.height = canvasHeight * scale;
      const ctx = canvas.getContext('2d');

      //      
      ctx.scale(scale, scale);
        
      //     ( )
      const frameImg = new Image();
      frameImg.crossOrigin = 'anonymous';
      
      await new Promise((resolve, reject) => {
        frameImg.onload = resolve;
        frameImg.onerror = reject;
        frameImg.src = frameImages[frame];
      });
      
      //     
      const positions = [
        { x: 23, y: 45, width: 267, height: 369 }, // photo1
        { x: 300, y: 45, width: 269, height: 369 }, // photo2
        { x: 23, y: 465, width: 269, height: 369 }, // photo3
        { x: 300, y: 465, width: 269, height: 369 }  // photo4
    ];
    
    for (let i = 0; i < capturedImageUrls.length; i++) {

        const img = new Image();
        img.crossOrigin = 'anonymous';
        
        await new Promise((resolve, reject) => {
        img.onload = resolve;
        img.onerror = reject;
        img.src = capturedImageUrls[i];

        });
        
        const pos = positions[i];
        
        // object-fit: cover   
        const imgAspect = img.width / img.height;
        const targetAspect = pos.width / pos.height;
        
        let drawWidth, drawHeight, offsetX, offsetY;
        
        if (imgAspect > targetAspect) {
        //    -    
        drawHeight = pos.height;
        drawWidth = drawHeight * imgAspect;
        offsetX = (pos.width - drawWidth) / 2;
        offsetY = 0;
        } else {
        //    -    
        drawWidth = pos.width;
        drawHeight = drawWidth / imgAspect;
        offsetX = 0;
        offsetY = (pos.height - drawHeight) / 2;
        }
        
        //   
        ctx.save();
        ctx.beginPath();
        ctx.rect(pos.x, pos.y, pos.width, pos.height);
        ctx.clip();
        
        //  
        ctx.drawImage(
        img,
        pos.x + offsetX,
        pos.y + offsetY,
        drawWidth,
        drawHeight
        );
        
        ctx.restore();
    }
    
    //        (   )
    ctx.drawImage(frameImg, 0, 0, canvasWidth, canvasHeight);
    
    console.log('Manual canvas creation completed');
    
    return new Promise((resolve) => {
        canvas.toBlob((blob) => {
        if (blob) {
            console.log('Blob created successfully, size:', blob.size);
        } else {
            console.error('Failed to create blob');
        }
        resolve(blob);
        }, "image/png", 1.0);
    });
    } catch (error) {
    console.error('Manual canvas creation error:', error);
    return null;
    }
}, [capturedImageUrls, frame, frameImages]);

//   
useEffect(() => {
    const preloadImages = async () => {
    const imagesToLoad = [
        frameImages[frame],
        ...(frame.startsWith("teacher") ? poseImages[frame] : [])
    ].filter(Boolean);

    const promises = imagesToLoad.map((src) => {
        return new Promise((resolve) => {
        const img = new Image();
        img.crossOrigin = "anonymous";
        img.onload = () => resolve();
        img.onerror = () => resolve();
        img.src = src;
        });
    });

    await Promise.all(promises);
    setImagesLoaded(true);
    console.log('All images preloaded');
    };

    preloadImages();
}, [frame, frameImages, poseImages]);

useEffect(() => {
    if (photoCount === 4 && imagesLoaded) {
    const saveFinalImage = async () => {
        console.log('Starting final capture for saving...');
        const resultBlob = await captureResultZoneAsBlob();
        if (resultBlob) {
        setFinalImageBlob(resultBlob);
        console.log('Final image saved to state');
        
        try{
            const storage = getStorage();
            const imageName = `photo_${Date.now()}.png`;
            const imageRef = ref(storage, `images/${imageName}`);
            await uploadBytes(imageRef, resultBlob);

            const db = getFirestore();
            await addDoc(collection(db, "emails"), {
            imagePath: `images/${imageName}`,
            timestamp: Date.now(),
            });
        }catch (error) {
            console.error('   : ', error);
        }
        setTimeout(() => {
            const imageUrl = URL.createObjectURL(resultBlob);
            navigate('/Email', { state: {
                                    previewImageUrl: imageUrl,
                                    frameSize: 'size2'
                                }
            });
        }, 1000);
        } else {
        console.error('Failed to capture result zone');
        }
    };

    //    
    const timer = setTimeout(saveFinalImage, 2000);
    return () => clearTimeout(timer);
    }
}, [photoCount, captureResultZoneAsBlob, imagesLoaded, navigate]);

return (
    <div className="PictureSize2-container">
    {showModal && <div className="PictureSize2-dark-overlay" />}
    {showModal && (
        <div className="PictureSize2-modal">
        <div className="PictureSize2-modal-content">
            <img className="PictureSize2-picIcon" src="/images/pictureIcon.png" alt=" " />
            <h2 className="PictureSize2-text1">   </h2>
            <p className="PictureSize2-text2">
              <span className="PictureSize2-text3"> 4</span> .
            </p>
        </div>
        </div>
    )}

    <div className="PictureSize2-Picture-background">
        <div className="PictureSize2-photoZone">
        {photoCount < 4 ? (
            <p className="PictureSize2-Photo-countdown">{countdown}</p>
        ) : (
            <p className="PictureSize2-Photo-countdown" style={{ visibility: "hidden" }}>0</p>
        )}
        <Webcam

            audio={false}
            ref={webcamRef}
            screenshotFormat="image/jpeg"
            className="PictureSize2-photo"
            mirrored
        />
        {frame.startsWith("teacher") && photoCount < 4 && (
            <img
            src={poseImages[frame][photoCount]}
            alt={`${frame}  ${photoCount + 1}`}
            className="PictureSize2-teacherPose"
            crossOrigin="anonymous"
            />
        )}
        </div>

        <div className="PictureSize2-resultZone" ref={resultZoneRef}>
        <img
            src={frameImages[frame] || ""}
            alt={`${frame} `}
            className="PictureSize2-frame"
            crossOrigin="anonymous"
        />
        {capturedImageUrls.map((url, index) => (
            <img
            key={index}
            src={url}
            alt={`Captured ${index + 1}`}
            className={`PictureSize2-photo${index + 1}`}
            crossOrigin="anonymous"
            />
        ))}
        </div>
    </div>
    </div>
);
};

export default PictureSize2;